import React, {useEffect, useState} from 'react';
import 'bootstrap/dist/css/bootstrap.css';
import {Alert} from 'react-bootstrap';
import './Products.scss';
import formatCurrency from '../Currency';
import { useDispatch , useSelector } from 'react-redux';
import { DetailsProduct } from '../store/Actions/ProductActions';
import LoadingBox from '../Components/LoadingBox';
import MessageBox from '../Components/MessageBox';
import {TiShoppingCart} from 'react-icons/ti';
import {BsTagFill} from 'react-icons/bs';
import {Link} from 'react-router-dom';
import { Button, Chip, CircularProgress, Typography } from '@material-ui/core';
import BreadCumb from '../Breadcrumbs/BreadCumb';

function ProductScreen(props) {
    
    const dispatch = useDispatch();
    const [qty, setqty]= useState(1);
    const ProductID = props.match.params.id;
    const productList = useSelector((state)=> state.productList);
    const {loading, errors, product} = productList;

    useEffect(()=>{
        dispatch(DetailsProduct(ProductID));
    } , [dispatch, ProductID]);


    const AddToCartHandler=()=>{
        props.history.push(`/cart/${ProductID}?qty=${qty}`);
    }

    if(!product) return null;

    return (
        <div className='product-screen-main-container'>
            {loading ? (<LoadingBox></LoadingBox>)
            : (<div>
            <div className='product-screen-container'>
            <BreadCumb path={product._id} title={product.Title}/>
            <div style={{display: "flex", flexWrap:"wrap", justifyContent: "space-between", padding: '1rem',gap: '1rem', borderTop: "1px solid #EBEBEB"}}>
            <img className='product-screen-img' src={product.Image}/>
            <div className='product-details-container'>
            <span className='product-detail-title'>{product.Title}</span>
            <span className='product-detail-price' variant='h5'>{formatCurrency(product.Price)}</span>
            {product.CountInStock > 0 && (
                <div style={{display: 'flex', gap: '1rem', alignItems: 'center'}}>
                <span className='product-quantity'>Quantity</span>
                <select className='single-product-view-quantity'
                            value={qty} onChange={(e)=> setqty(e.target.value)}>
                            {[...Array(product.CountInStock).keys()].map(
                            (x)=>(<option key={x+1} value={x+1}>{x+1}</option>))}
                </select> 
            </div>)}
            {product.CountInStock > 0 ? (<button onClick={AddToCartHandler} className='btn-add-cart'>Add To Cart</button>) : (<Alert variant='danger'>OUT OF STOCK</Alert>)}
            <div style={{display: 'flex', flexDirection: "column", gap: '10px'}}>
                <div className='delivery-info'><BsTagFill className='tag-icon'/><span>Delivery within 24 hours</span></div>
                <div className='delivery-info'><BsTagFill className='tag-icon'/><span>Lowcost delivey charges</span></div>
                <div className='delivery-info'><BsTagFill className='tag-icon'/>{product.CountInStock > 0 ? (<span>In stock</span>): (<span>Out of stock</span>)}</div>
            </div>
            </div>
            
            <div className='add-cart-container'>
            <div style={{display:'flex',alignItems: 'center', gap: '1rem'}}>
                <span className='product-quantity'>Available Sizes</span>
                <div style={{display:'flex', alignItems:'center', gap: "5px"}}>
                {
                    product.AvailableSize.map((size)=>(
                        <div className='available-sizes'><span>{size}</span></div>
                    ))
                }
                </div>

            </div>
            <p className='product-Description'>{product.Description}</p>
            </div>

            </div>
            </div>
            </div>)}
        </div>
        
    )
}

export default ProductScreen;
