import React, {useState} from 'react';
import CheckOutScreen from '../CheckOut/CheckOutScreen';
import {Form} from 'react-bootstrap';
import './Shipping.scss';
import {useDispatch,useSelector} from 'react-redux';
import { SaveShippingAddress } from '../store/Actions/CartActions';
import {Typography} from '@material-ui/core';
import FieldInput from '../Forms/FieldInput';

function Shipping(props) {

    const UserSign = useSelector(state=>state.UserSign);
    const{UserInfo}= UserSign;

    const Cart = useSelector(state=>state.Cart);
    const {ShippingAddress}= Cart;

    if(!UserInfo){
        props.history.push('/login');
    }

    const [FullName,setFullName]= useState(ShippingAddress.FullName);
    const [Address, setAddress]= useState(ShippingAddress.Address);
    const [City, setCity]= useState(ShippingAddress.City);
    const [Postalcode, setPostalcode]= useState(ShippingAddress.Postalcode);
    const [Country,setCountry]= useState(ShippingAddress.Country);

    const dispatch = useDispatch();

    const OnSubmitHandler=(e)=>{
        e.preventDefault();
        dispatch(SaveShippingAddress({FullName, Address, City, Postalcode, Country}));
        props.history.push('/payments');
    }

    return (
        <div className='main-shipping-container'>
            <div className='shipping-container'>
            <CheckOutScreen step1 step2></CheckOutScreen>
            <div className='container-shipping'>
            <span className='shipping-form-title'>Shipping Address</span>
            <div className='shipping-form'>
            <form onSubmit={OnSubmitHandler}>
            <FieldInput
            label="Full Name"
            value={FullName}
            onChange={(e)=> setFullName(e.target.value)}/>
            <FieldInput
            label="Address"
            value={Address}
            onChange={(e)=> setAddress(e.target.value)}/>
            <FieldInput
            label="City"
            value={City}
            onChange={(e)=> setCity(e.target.value)}/>
            <FieldInput
            label="Postal Code"
            value={Postalcode}
            onChange={(e)=> setPostalcode(e.target.value)}/>
            <FieldInput
            label="Country"
            value={Country}
            onChange={(e)=> setCountry(e.target.value)}/>
            <button className='form-shipping-btn' type="submit">Continue</button>
            </form>
            </div>
            </div>
            </div>
        </div>
    )
}

export default Shipping;
