import React,{useState} from 'react';
import CheckOutScreen from '../CheckOut/CheckOutScreen';
import './Shipping.scss';
import {Form, Button} from 'react-bootstrap';
import {useDispatch, useSelector} from 'react-redux';
import { SavePaymentMethod } from '../store/Actions/CartActions';


function Payments(props) {

    const Cart = useSelector(state=> state.Cart);
    const {ShippingAddress}= Cart;

    if(!ShippingAddress.Address){
        props.history.push('/shipping');
    }

    const dispatch = useDispatch();
    const [PaymentMethod, setPaymentMethod]= useState('');

    const OnSubmitHandler=(e)=>{
        e.preventDefault();
        dispatch(SavePaymentMethod(PaymentMethod));
        props.history.push('/placeorder');
    }

    return (
        <div className='main-shipping-container'>
            <div className='shipping-container'>
            <CheckOutScreen step1 step2 step3></CheckOutScreen>
            <div className='container-shipping'>
            <span className='shipping-form-title'>Payment Method</span>
            <div className='shipping-form'>
            <form onSubmit={OnSubmitHandler}>
            
               <div><input type='radio'
               id='Paypal'
               value='PayPal'
               required
               checked
               className='mr-2'
               name='PaymentMethod'
               onChange={(e)=>setPaymentMethod(e.target.value)}/>
               <label>PayPal</label></div>

              <div><input type='radio'
               id='Stripe'
               value='Stripe'
               required
               className='mr-2'
               name='PaymentMethod'
               onChange={(e)=>setPaymentMethod(e.target.value)}/>
               <label>Stripe</label></div>
               
               <button className='form-shipping-btn' type='submit'>Continue</button> 
            </form>  
            </div>  
            </div>
            </div>       
        </div> 
    )
}

export default Payments;
