import Axios from "axios";
import { END_LOADING, PRODUCT_DETAILS_FAIL, PRODUCT_DETAILS_REQUEST, PRODUCT_DETAILS_SUCCESS, PRODUCT_LIST_FAIL, PRODUCT_LIST_REQUEST, PRODUCT_LIST_SUCCESS, PRODUCT_SEARCH_FAIL, PRODUCT_SEARCH_REQUEST, PRODUCT_SEARCH_SUCCESS, START_LOADING } from "../Constants/ProductConstant";
 

export const ListProducts =()=> async(dispatch)=>{
    dispatch({type: START_LOADING});
    try{
    const {data} = await Axios.get('/api/products');
    dispatch({type: PRODUCT_LIST_SUCCESS, payload: data});
    dispatch({type: END_LOADING});
    }catch(error){
    dispatch({type: PRODUCT_LIST_FAIL, payload: error.response && error.response.data.message
        ? error.response.data.message : error.message});
    }
}

export const DetailsProduct=(productID)=> async(dispatch)=>{
    dispatch({type: START_LOADING, payload: productID});
    try{
        const {data} = await Axios.get(`/api/products/${productID}`);
        dispatch({type: PRODUCT_DETAILS_SUCCESS, payload: data});
        dispatch({type: END_LOADING});
    }catch(error){
     dispatch({type: PRODUCT_DETAILS_FAIL,
         payload: error.response && error.response.data.message
        ? error.response.data.message : error.message});
    }
}

export const searchProducts=(searchQuery)=>async(dispatch)=>{
    dispatch({type: START_LOADING, payload: searchQuery});
    try{
        const {data} = await Axios.post(`/api/products/search?searchQuery=${searchQuery.search || 'none'}`);
        dispatch({type: PRODUCT_SEARCH_SUCCESS, payload: data});
        dispatch({type: END_LOADING});
    }catch(error){
        dispatch({type: PRODUCT_SEARCH_FAIL,
            payload: error.response && error.response.data.message
           ? error.response.data.message : error.message});
    }
    
}