import React from 'react';
import {span, Breadcrumbs} from '@material-ui/core';
import {FaHome} from 'react-icons/fa';
import {Link} from 'react-router-dom';
import './breadcrumb.scss';

function BreadCumb(props) {
    return (
        <div className='breadcrumb'>
            <Breadcrumbs  aria-label="breadcrumb">
            <Link className='bread-crumb-links' to='/'><div className='crumb-items'><FaHome className='home-icon'/><span className='crumb-title'>Home</span></div></Link>
            <Link className='bread-crumb-links' to={`/product/${props.path}`}><div className='crumb-items'><span className='crumb-title'>{props.title}</span></div></Link>
        </Breadcrumbs>
        </div>
    )
}

export default BreadCumb;
