import React from 'react';
import './CheckOutScreen.scss';
import {FiChevronsRight} from 'react-icons/fi';

function CheckOutScreen(props) {
    return (
        <div className='checkout-screens main-checkout-steps'>
            <div className={props.step1 ? 'active' : 'deactive'}><span>Sign-In</span></div>
            <div className={props.step2 ? 'active' : 'deactive'}><span>Shipping</span></div>
            <div className={props.step3 ? 'active' : 'deactive'}><span>Payments</span></div>
            <div className={props.step4 ? 'active' : 'deactive'}><span>Place Order</span></div>
        </div>
    )
}

export default CheckOutScreen;
