import { PRODUCT_DETAILS_FAIL, PRODUCT_DETAILS_REQUEST, PRODUCT_DETAILS_SUCCESS,
     PRODUCT_LIST_FAIL, PRODUCT_LIST_REQUEST, PRODUCT_LIST_SUCCESS,
    PRODUCT_SEARCH_REQUEST,PRODUCT_SEARCH_SUCCESS,PRODUCT_SEARCH_FAIL, START_LOADING, END_LOADING } from "../Constants/ProductConstant";



export const productSearchReducer=(state={loading: true, products:[]}, action)=>{
    switch(action.type){
        case PRODUCT_LIST_REQUEST:
            return{loading: true};
        case PRODUCT_LIST_SUCCESS:
            return {loading: false, products: action.payload};
        case PRODUCT_LIST_FAIL:
            return {loading: false, error: action.payload};
        default:
            return state;
    }
}


export const productReducer=(state={loading: true, products: [], errors: []}, action)=>{
    switch(action.type){
        case START_LOADING:
            return {...state, loading: true};
        case END_LOADING:
            return {...state, loading: false};
        case PRODUCT_LIST_SUCCESS:
            return {...state, products: action.payload};
        case PRODUCT_LIST_FAIL:
            return {...state, errors: action.payload};
        case PRODUCT_DETAILS_SUCCESS:
            return {...state, product: action.payload};
        case PRODUCT_DETAILS_FAIL:
            return {...state, errors: action.payload};
        case PRODUCT_SEARCH_SUCCESS:
            return {...state, products: action.payload};
        case PRODUCT_SEARCH_FAIL:
            return {...state, errors: action.payload};
        default:
            return state;
    }
}