import React, {useState, useEffect} from 'react';
import{useDispatch, useSelector} from 'react-redux';
import {Link} from 'react-router-dom';
import './login.scss';
import {RegisterUser} from '../store/Actions/UserActions';
import LoadingBox from '../Components/LoadingBox';
import MessageBox from '../Components/MessageBox';
import FieldInput from '../Forms/FieldInput';

function Register(props) {

    const dispatch = useDispatch();
    const [Name, setName]= useState('');
    const [Email, setemail]= useState('');
    const [Password, setpassword] = useState('');
    const [ConfirmPassword, setconfirmpassword] = useState('');

    const redirect = (props.location.search) ? 
    (props.location.search.split("=")[1])
    : ('/');

    const Register = useSelector(state=> state.Register);
    const {UserInfo, loading, error}= Register;

    const OnSubmitHandler=(e)=>{
        e.preventDefault();
        if(Password !== ConfirmPassword){
            alert('Password Does Not Matched');
        }else{
            dispatch(RegisterUser(Name, Email, Password)); 
        }
    }

    useEffect(()=>{
        if(UserInfo){
            props.history.push(redirect);
        }
    },[props.history, redirect, UserInfo]);



    return (
        <div className='main-login-container'>
            <div className='login-container'>

            <div className='login-left-container'>
            <span className='login-title'>Create A New SNMART Account</span>
            <div style={{display: 'flex', flexDirection: 'column', gap: '10px', borderBottom: "1px solid #EBEBEB"}}>
                <h6 style={{fontWeight: '500',color: '#37475A'}}>Account Benefits</h6>
                <p style={{fontSize: '13px', textAlign: 'justify'}}>If you create an account with us, you will get additional benefits such as order history, bonus cash and more.</p>
                
            </div>
            <button className='btn-login'><Link to='/login' style={{color: 'white', textDecoration: 'none'}}>Sign In</Link></button>

            </div>

            <div className='login-right-container'>
            <span className='login-title'>Create your free account</span>
            <form onSubmit={OnSubmitHandler}>
            {loading && (<LoadingBox></LoadingBox>)}
                       {error && (<MessageBox>{error}</MessageBox>)}
            <FieldInput label="Name" value={Name} onChange={(e)=> setName(e.target.value)}/>
            <FieldInput label='Email Address' value={Email} onChange={(e)=> setemail(e.target.value)}/>
            <FieldInput label="Password" type='password' value={Password} onChange={(e)=> setpassword(e.target.value)}/>
            <FieldInput label="Confirm Password" type="password" value={ConfirmPassword} onChange={(e)=> setconfirmpassword(e.target.value)}/>
            <button type='submit' className='btn-login'>Create Account</button>
            </form>
            </div> 
                


    
            </div>         
        </div>
    )
}

export default Register;
