import React from 'react';
import formatCurrency from '../Currency';
import './Header.scss';
import {CgClose} from 'react-icons/cg';
import {Link} from 'react-router-dom';
import { useDispatch } from 'react-redux';
import {RemoveFromCart} from '../store/Actions/CartActions';

function ViewCart(props) {
    
    const {CartItems} = props;
    const dispatch = useDispatch();
    const RemoveFromCartHandler=(id)=>{
        dispatch(RemoveFromCart(id));
    }

    return (
        <div className='view-cart-main-container'>
            <button className='btn-view-cart' onClick={props.closeshowcart}>
                <Link style={{textDecoration: 'none', color: '#37475A'}} to='/cart'>View Cart</Link></button>
            <div className='view-cart-container'>
            {CartItems && CartItems.map((item)=>(
                <div className='view-cart-item' >
                    <img style={{width: '2.5rem'}} src={item.Image}/>
                    <div style={{display: 'flex', flexDirection: 'column'}}>
                    <Link style={{textDecoration: 'none'}} to={`/product/${item}`}><span className='view-cart-title'>{item.Title.substr(0,20)}</span></Link>
                    <span className='view-cart-price'>{item.qty} X {formatCurrency(item.Price)}</span>
                    </div>
                    <CgClose onClick={()=> RemoveFromCartHandler(item._id)} className='remove-view-cart'/>
                </div>
            ))}
            </div>
        <button className='btn-checkout' onClick={props.closeshowcart}>
            <Link style={{textDecoration: 'none', color: 'white'}} to='/shipping'>Checkout</Link></button>
        </div>
    )
}

export default ViewCart;
