import React from 'react';
import formatCurrency from '../Currency';
import {Alert} from 'react-bootstrap';
import {Link} from 'react-router-dom';

function ProductModal(props) {
    const {product} = props;
    return (
        <div className='product-modal-div'>
            <div style={{display: "flex", justifyContent: "space-between", flexWrap: 'wrap'}}>
            <img className='product-modal-img' src={product.Image}/>
            <div className='product-modal-right'>
                <h5 className='product-quantity'>{product.Title}</h5>
                <span className='product-detail-price'>{formatCurrency(product.Price)}</span>
                <div className='product-instock'>{product.CountInStock > 0 ? (<Alert variant='success'>In Stock</Alert>) : (<Alert variant='danger'>Out Of Stock</Alert>)}</div>
                <div style={{display:'flex', gap: "1rem", alignItems: 'center'}}>
                    <span className='product-quantity'>Available Sizes</span>
                    <div style={{display: 'flex', gap: '1rem'}}>
                        {product.AvailableSize.map((size)=>(
                            <div className='available-sizes'>{size}</div>
                        ))}
                    </div>
                </div>
                <p className='product-Description'>{product.Description}</p>
            </div>
            </div>
            <div className='product-modal-bottom'>
                <Link to={`/product/${product._id}`}><button className='btn-view-details'>View Details</button></Link>
            </div>
        </div>
    )
}

export default ProductModal;
