import React, {useEffect} from 'react';
import CheckOutScreen from '../CheckOut/CheckOutScreen';
import './order.scss';
import {Button} from 'react-bootstrap';
import {useDispatch, useSelector} from 'react-redux';
import formatCurrency from '../Currency';
import {Link} from 'react-router-dom';
import { CREATE_ORDER_RESET } from '../store/Constants/PlaceOrderConstants';
import { CreateOrder } from '../store/Actions/PlaceOrderActions';
import LoadingBox from '../Components/LoadingBox';
import MessageBox from '../Components/MessageBox';

function PlaceOrder(props) {

    const Cart = useSelector(state=> state.Cart);
    const {ShippingAddress, PaymentMethod, CartItems} = Cart;

    if(!PaymentMethod){
        props.history.push('/payments');
    }

    const dispatch = useDispatch();
    const OrderCreate = useSelector(state=> state.OrderCreate);
    const {loading, success, error, order} = OrderCreate;

    const ToPrice = (num)=> Number(num.toFixed(2)); //'5.123'=> '5.12'=> 5.12
    Cart.ItemsPrice = ToPrice(Cart.CartItems.reduce((a,c)=> a + c.qty* c.Price, 0));

    Cart.ShippingPrice = ((Cart.ItemsPrice > 100)?(ToPrice(0)):(ToPrice(10)));
    Cart.TaxPrice = ToPrice(0.15*(Cart.ItemsPrice));
    Cart.TotalPrice = (Cart.ItemsPrice + Cart.TaxPrice + Cart.ShippingPrice);

    const PlaceOrderSubmitHandler=()=>{
        dispatch(CreateOrder({...Cart, OrderItems: Cart.CartItems}));
    }
    
    useEffect(()=>{
        if(success){
            props.history.push(`/order/${order._id}`);
            dispatch({type: CREATE_ORDER_RESET});
        }
    }, [dispatch, order, props.history, success]);

    return (
       <div className='main-place-order-container'>
          <div className='place-order-container'>
          <CheckOutScreen step1 step2 step3/>
         <div className='place-order-card-container'>
            <div className='order-card'>
             <h5 className='order-card-title'>Shipping Address</h5>
                 <span>Full Name : {ShippingAddress.FullName}</span>
                 <span>Address : {ShippingAddress.Address}</span>
                 <span>City : {ShippingAddress.City}</span>
                 <span>Postal Code :{ShippingAddress.Postalcode}</span>
                 <span>Country : {ShippingAddress.Country}</span>
            </div>

            <div className='order-card'>
             <h5 className='order-card-title'>Payment Method</h5>
                 <span>Payments : {PaymentMethod}</span>
            </div>

            <div className='order-card'>
             <h5 className='order-card-title'>Ordered Items</h5>
             {CartItems.map((item)=>(
                 <div className='place-order-items'>
                     <img style={{width: "3rem"}} src={item.Image}/>
                     <div style={{display: "flex", flexDirection: 'column', gap: '5px'}}>
                     <span className='place-order-item-span'>{item.Title.substr(0,20)}{`...`}</span>
                     <span className='place-order-item-span'>{item.qty} X {formatCurrency(item.Price)} = {formatCurrency((item.qty)*(item.Price))}</span>
                     </div>
                     
                 </div>
             ))}
            </div>

            <div className='order-card'>
            <h5 className='order-card-title'>Order Summary</h5>
            <span>Sub Total : {formatCurrency(Cart.ItemsPrice)}</span>
            <span>Shipping Cost : {formatCurrency(Cart.ShippingPrice)}</span>
            <span>Taxes : {formatCurrency(Cart.TaxPrice)}</span>
            <span>Order Total : {formatCurrency(Cart.TotalPrice)}</span>
            <button onClick={PlaceOrderSubmitHandler}
                   disabled={Cart.CartItems.length === 0} className='placer-order-btn'>Place Order</button>
            </div>

          </div>
        </div>

    </div>
    )
}

export default PlaceOrder;
