import React from 'react';
import './Menu.scss';
import {Link} from 'react-router-dom';
import {SidebarData} from './SidebarData';
import {IoCloseOutline} from 'react-icons/io5';
import {FaUserCircle} from 'react-icons/fa';
import {useSelector} from 'react-redux';

function Menu(props) {
    const UserSign = useSelector(state=> state.UserSign);
    const {UserInfo}= UserSign;
    return (
           <div className='menu'>
            <div className='side-menu-profile-container'>
                <div><span className='user-info'>Our Top Categories</span></div>
            <div><IoCloseOutline className='close-menu-icon' onClick={props.closesidebar}/></div>
            </div>
           <div className='side-menu-container'>{SidebarData.map((sblink)=>(
               <div className='menu-item' key={sblink.Title}>
                   <Link to={sblink.path} onClick={props.closesidebar} className={sblink.CName}>{sblink.Title}</Link></div>
           ))}</div>
        </div>
    )
}

export default Menu;
