import React from 'react';
import './form.scss';

function FieldInput(props) {
    const {name,type,id,value,placeholder,label,onChange,onBlur} = props;
    return (
        <div className='form-group'>
            <label className='field-input-lbl'>{label}</label>
            <input className='field-input-txt'
             type={type} 
             placeholder={placeholder} 
             value={value} name={name} 
             id={id}
             onBlur={onBlur}
             onChange={onChange}/>
        </div>
    )
}

export default FieldInput;
