import React, {useEffect, useState} from 'react';
import './order.scss';
import {useDispatch, useSelector} from 'react-redux';
import formatCurrency from '../Currency';
import {Link} from 'react-router-dom';
import { DetailsOrder, PayOrder } from '../store/Actions/PlaceOrderActions';
import LoadingBox from '../Components/LoadingBox';
import MessageBox from '../Components/MessageBox';
import Axios from 'axios';
import {PayPalButton} from 'react-paypal-button-v2';
import { ORDER_PAY_RESET } from '../store/Constants/PlaceOrderConstants';

function OrderDetailsView(props) {

    const orderId = props.match.params.id;
    const OrderDetails = useSelector((state)=>state.OrderDetails);
    const {order, loading, error} = OrderDetails;

    const [sdkReady, setsdkReady] = useState(false);

    const OrderPay = useSelector((state)=> state.OrderPay);
    const {loading: loadingpay, 
        error: errorpay, success: successpay} = OrderPay;

    const dispatch = useDispatch();

    useEffect(()=>{
        const AddPayPalScript = async()=>{
            const {data} = await Axios.get('/api/config/paypal');
            const script = document.createElement("script");
            script.type = "text/javascript";
            script.src = `https://www.paypal.com/sdk/js?client-id=${data}`;
            script.async = true;
            script.onload=()=>{
            setsdkReady(true);
            };
            document.body.appendChild(script);
        };

        if(!order || successpay || (order && order._id !== orderId)){
            dispatch({type: ORDER_PAY_RESET});
            dispatch(DetailsOrder(orderId));
        }else{
            if(!order.IsPaid){
                if(!window.paypal){
                    AddPayPalScript();
                }else{
                    setsdkReady(true);
                }
            }
        }
    }, [dispatch, order,orderId, sdkReady]);


    const SuccessPaymentHandler=(PaymentResults)=>{
        dispatch(PayOrder(order, PaymentResults));
    }

    return (
        (loading)?(<LoadingBox></LoadingBox>):
        (error)? (<MessageBox>{error}</MessageBox>)
        :
        (<div className='main-place-order-container'>
            <div className='place-order-container'>
            <span className='order-id'>Order : {order._id}</span>
            <div className='place-order-card-container'>
            <div className='order-card'>
             <h5 className='order-card-title'>Shipping Address</h5>
                 <span>Full Name : {order.ShippingAddress.FullName}</span>
                 <span>Address : {order.ShippingAddress.Address}</span>
                 <span>City : {order.ShippingAddress.City}</span>
                 <span>Postal Code :{order.ShippingAddress.Postalcode}</span>
                 <span>Country : {order.ShippingAddress.Country}</span>
                 <span>{(order.IsDelivered)?(<MessageBox>Delivered at {order.IsDelivered}</MessageBox>)
                : (<MessageBox>Not Delivered</MessageBox>)}</span>
            </div>

            <div className='order-card'>
             <h5 className='order-card-title'>Payment Method</h5>
                 <span>Payments : {order.PaymentMethod}</span>
                 <span>{(order.IsPaid)?(<MessageBox>Paid at {order.PaidAt}</MessageBox>)
                : (<MessageBox>Not Paid</MessageBox>)}</span>
            </div>

            <div className='order-card'>
             <h5 className='order-card-title'>Ordered Items</h5>
             {order.OrderItems.map((item)=>(
                 <div className='place-order-items'>
                     <img style={{width: "3rem"}} src={item.Image}/>
                     <div style={{display: "flex", flexDirection: 'column', gap: '5px'}}>
                     <span className='place-order-item-span'>{item.Title.substr(0,20)}{`...`}</span>
                     <span className='place-order-item-span'>{item.qty} X {formatCurrency(item.Price)} = {formatCurrency((item.qty)*(item.Price))}</span>
                     </div>
                     
                 </div>
             ))}
            </div>

            <div className='order-card'>
            <h5 className='order-card-title'>Order Summary</h5>
            <span>Sub Total : {formatCurrency(order.ItemsPrice)}</span>
            <span>Shipping Cost : {formatCurrency(order.ShippingPrice)}</span>
            <span>Taxes : {formatCurrency(order.TaxPrice)}</span>
            <span>Order Total : {formatCurrency(order.TotalPrice)}</span>
            
            {(!order.IsPaid) && (
                        <div>
                            {(!sdkReady) ? (<LoadingBox></LoadingBox>):
                                (
                                <>
                                {errorpay && (<MessageBox>{errorpay}</MessageBox>)}
                                {loadingpay && (<LoadingBox></LoadingBox>)}
                                <PayPalButton amount={order.TotalPrice}
                                onSuccess={SuccessPaymentHandler}></PayPalButton>
                                </>)}</div>
                            )}        
            </div>


            </div>
            </div>
          
    </div>)

        

    )
}

export default OrderDetailsView;
