import React, {useState} from 'react';
import {Link} from 'react-router-dom';
import './Products.scss';
import {Button} from '@material-ui/core';
import formatCurrency from '../Currency';
import { Alert, Modal} from 'react-bootstrap';
import {Card,CardActions, CardContent, Typography, CardMedia,Chip} from '@material-ui/core';
import ProductModal from './ProductModal';

export default function Products (props){
  const [show, setShow] = useState(false);

  const handleClose = () => setShow(false);
  const handleShow = () => setShow(true);
    const { product } = props;

  
        return (
            <div>
              <Card className='single-card'>
              <Link to={`/product/${product._id}`}><img src={product.Image} className='product-image'/></Link>
              <span className='product-title'>{product.Title.substr(0,20)}</span>
              
              {/* <div style={{display:"flex", gap: "0.5rem", alignItems: 'center', width: "100%", justifyContent: "center"}}>
              {product.AvailableSize && product.AvailableSize.map((size)=>(
                <Chip label={size} className='sizes'/>
              ))}
              </div> */}
              <div className='product-btn-container'>
              <span className='product-price'>{formatCurrency(product.Price)}</span>
              <span className='btn-view-product' onClick={handleShow}>View</span>
              </div>
            </Card>
            <Modal
            className='product-modal'
            size='lg'  
            aria-labelledby="contained-modal-title-vcenter"
            centered 
            show={show} 
            onHide={handleClose}>
              <Modal.Header closeButton>
              <span className='modal-title'>
              {product.Title}
              </span>
              </Modal.Header>
              <ProductModal product={product}/>
            </Modal>
            </div>
        )
    
}